* {
  font-family: Courier New, Courier, monospace;
}

.title {
  text-align: center;
}

.gallery {
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: center;
  gap: 30px;
  list-style: none;
  display: grid;
}

.gallery-item {
  align-items: center;
  display: flex;
  position: relative;
}

.gallery-picture {
  width: 100%;
  box-sizing: border-box;
  border: 3px solid #000;
  padding: 20px;
}

.form {
  height: 60px;
  justify-content: center;
  display: flex;
}

.input {
  border: 2px solid #000;
  margin-right: 15px;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
}

.button {
  cursor: pointer;
  border: none;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: bold;
  transition: all .25s linear;
  display: inline-block;
  box-shadow: 0 0 4px #000;
}

.find-button {
  margin-right: 15px;
}

.reset-button {
  border-radius: 50%;
  align-items: center;
  padding: 0 15px;
  display: flex;
}

.reset-button__svg {
  width: 30px;
  height: 30px;
}

.load-more-button {
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.button:hover, .button:focus {
  box-shadow: 0 0 8px #000;
}

.reset-button:hover, .reset-button:focus {
  transform: rotate(270deg);
}

.button:disabled:hover {
  cursor: auto;
  box-shadow: 0 0 4px #000;
}

.text {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

